* {
  box-sizing: border-box;
  list-style-type: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #585f63;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

* {
  margin: 0;
  padding: 0;
  font-family: Quicksand, sans-serif;
  font-weight: 400;
}

.flexbox {
  width: 100%;
  height: 100%;
  background: #585f63;
  align-items: center;
  padding-left: 20px;
  display: flex;
}

.search {
  margin: 10px;
}

.search > h3 {
  font-weight: normal;
}

.search > h1, .search > h3 {
  color: #fff;
  text-shadow: 1px 1px #2b2b2b;
  margin-top: 15px;
  margin-bottom: 15px;
}

.search > div {
  filter: drop-shadow(1px 1px #2b2b2b);
  display: inline-block;
  position: relative;
}

.search > div:after {
  content: "";
  width: 4px;
  height: 20px;
  background: #fff;
  position: absolute;
  top: 27px;
  right: -4px;
  transform: rotate(135deg);
}

.search > div > input {
  color: #fff;
  width: 35px;
  height: 35px;
  background: none;
  border: 3px solid #fff;
  border-radius: 35px;
  outline: none;
  padding: 10px;
  font-size: 16px;
  transition: width .5s;
}

.search > div > input::-ms-input-placeholder {
  color: #efefef;
  opacity: 0;
  transition: opacity .15s ease-out;
}

.search > div > input::placeholder {
  color: #efefef;
  opacity: 0;
  transition: opacity .15s ease-out;
}

.search > div > input:focus::-ms-input-placeholder {
  opacity: 1;
}

.search > div > input:focus::placeholder {
  opacity: 1;
}

.search > div > input:focus, .search > div > input:not(:placeholder-shown) {
  width: 230px;
}

.country-list {
  margin-top: 20px;
  padding-left: 30px;
}

.country-list__item {
  background: linear-gradient(to right, rgba(162, 192, 231, .2), rgba(162, 192, 231, .02), rgba(162, 192, 231, 0));
  border-radius: 3px;
  align-items: center;
  margin-top: 10px;
  padding: 5px 10px;
  display: flex;
}

.country-list__item > p {
  color: #efefef;
  margin-left: 10px;
  font-family: Quicksand, sans-serif;
  font-size: 30px;
  font-weight: 100;
}

.country-info {
  margin-top: 20px;
  padding-left: 30px;
}

.country-info__item {
  background: linear-gradient(to right, rgba(162, 192, 231, .2), rgba(162, 192, 231, .02), rgba(162, 192, 231, 0));
  border-radius: 3px;
  padding: 5px 10px;
}

.country-info__header {
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.country-info__header > p {
  font-family: Quicksand, sans-serif;
  font-size: 50px;
  font-weight: 600;
}

.country-info__img {
  width: 60px;
  margin-right: 20px;
}

